<template>
    <div>
      <NavBar title="备忘录" @click-back="backFn"/>
      <h1>todolist</h1>
       <swiper class="swiper" ref="mySwiper" :options="swiperOptions">
        <swiper-slide>Slide 1</swiper-slide>
        <swiper-slide>Slide 2</swiper-slide>
        <swiper-slide>Slide 3</swiper-slide>
        <swiper-slide>Slide 4</swiper-slide>
        <swiper-slide>Slide 5</swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>

      <input type="text" v-model="title">
      <button @click="addFn">添加</button>
      <ul>
        <!-- 
          TODO. 
          发现：在框架里面写遍历必须得写v-bind:key="唯一标识"
          原因：先不说  涉及到虚拟DOM、diff算法
        -->
        <li v-for="(item,index) in todos" v-bind:key="index">
          {{item.title}}
        </li>
      </ul>
    </div>
</template>
<script>
import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name:'Navbar',
  components: {
    Swiper,
    SwiperSlide,
    // NavBar,
  },
  directives: {
    swiper: directive
  },
  methods: {
    backFn() {
      alert('back')
    },
    addFn() {
      this.todos.push({
        id: this.todos.length+1,
        title: this.title
      })
    }
},
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper
    }
  },
  mounted() {
    console.log('Current Swiper instance object', this.swiper)
    this.swiper.slideTo(3, 1000, false)

    // fetch('http://kg.zhaodashen.cn/v2/cate/index.php')
    fetch('/api/v2/cate/index.php')
    .then(res => res.json())   
    .then(res => {
      console.log(res)
    })
  },
  data() {
    return {
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination'
        },
      }, 
      // Some Swiper option/callback...
      title: '',
      todos: [
        {id:1, title: '吃饭'},
        {id:2, title: '睡觉'},
        {id:3, title: '臭脚'},
      ]
    }
  }
}
</script>
<style scoped>
.swiper  {
  height: 500px;
}
</style>