// 之前：引入vue库
// 现在：1 模块化、2 导入
import Vue from 'vue'        // 导入vue模块
import App from './App.vue'  // 导入vue单文件组件 vetur插件才会高亮
import router from './router'// 路由（留心：在vue中导入文件  先找文件，找不到就找同名目录/index文件
// 说明：VUE配置
Vue.config.productionTip = false


import Navbar from '@/components/NavBar.vue'
Vue.use({
  // install 是默认的方法。当外界在 use 这个组件的时候，就会调用本身的 install 方法，同时传一个 Vue 这个类的参数。
  install:function(Vue){
      Vue.component('NavBar', Navbar)
  }
});

new Vue({
  // 新注入路由 
  router, 
  render: h => h(App)
}).$mount('#app')
// $mount == $el

import QFUI from '@/qf-ui'
Vue.use(QFUI) 